import axios from 'axios'

export function getActionPermission(action) {
  return axios.get('./index.php', {
    params: {
      c: 'filters',
      f: 'permisoAccion',
      action: action
    }
  })
    .then(res => {
      return res.data
    })
    .catch(error => {
      return []
    })
}

